#navbar-container{position:fixed;top:0;height:55px;width:100vw;z-index:2;background:white;border-bottom:1px solid black;display:flex;justify-content:space-around;align-items:center}#navbar-container span{font-family:'Crushed', cursive;font-size:1.3em;text-transform:uppercase;margin:0.1em}#navbar-container #nombre{font-weight:800;color:#303841}#navbar-container #apellido{font-weight:200;color:#3a4750}#navbar-container button{display:none;background:transparent;z-index:3;border:none;cursor:pointer}#navbar-container button svg{height:1.2em;width:1.2em;fill:#303841}#navbar-container button svg:hover{fill:#3a4750}.option{display:flex;align-items:center;list-style-type:none}.option li{margin:1em;text-transform:uppercase;font-weight:600;font-family:'Baloo Tamma 2', cursive;cursor:pointer;color:#303841}.option li:hover{color:#d72323}.dropDown{position:fixed;top:55px;width:100vw;background:white;z-index:2;display:none;flex-direction:column;align-items:stretch;clip-path:circle(0 at 90% 0%);-webkit-clip-path:circle(0 at 90% 0%);transition:all 0.4s ease-out;pointer-events:none}.dropDown .option{flex-direction:column;height:100%}.dropDown .option li{width:100%;padding:1em;margin:0;display:flex;justify-content:center;align-items:center;flex:1}.dropDown .option li:hover{background:#ccc}.open{pointer-events:all;clip-path:circle(100vh at 90% 0%);-webkit-clip-path:circle(100vh at 90% 0%)}@media only screen and (min-device-width: 320px) and (max-device-width: 556px){#navbar-container ul{display:none}#navbar-container button{display:flex}.dropDown{display:flex}}@media only screen and (min-device-height: 320px) and (max-device-height: 556px){#navbar-container ul{display:none}#navbar-container button{display:flex}.dropDown{display:flex}}
